* {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  font-family: "Rubik", sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

header,
section {
  width: 100%;
  overflow: hidden;
  color: #fff;
}

header {
  height: 100vh;
  background: url("images/Laptop\ &\ BG.png");
}

.header-content {
  width: 759px;
  vertical-align: middle;
  position: absolute;
  top: 43%;
  left: 160px;
  transform: translateY(-43%);
}

.header-img-container {
  width: 55%;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.header-img {
  width: 100%;
}

h1 {
  font-style: normal;
  font-weight: 800;
  font-size: 58px;
  line-height: 72px;
  letter-spacing: -0.03em;
}

.heading-text {
  width: 600px;
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 30px;
  margin-top: 22px;
}

nav {
  margin-top: 26px;
}

nav ul li {
  display: inline-block;
}

nav a {
  display: inline-block;
  min-width: 180px;
  border-radius: 8px;
  color: #05445e;
  background-color: #fff;
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 800;
  font-size: 17px;
  line-height: 48px;
  text-align: center;
  letter-spacing: 1px;
  box-shadow: 0px 0px 1px 1px rgba(255, 255, 255, 0.3);
}

.features {
  margin-top: 42px;
  width: 700px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 18px;
}

.features li {
  width: 300px;
  list-style: none;
  display: inline-block;
  font-size: 18px;
}

.features li::before {
  content: "";
  display: inline-block;
  width: 50px;
  height: 50px;
  box-shadow: 0px 0px 2px 0px rgba(255, 255, 255, 0.652);
  border-radius: 8px;
  margin-right: 18px;
  vertical-align: middle;
}

.button-secondary {
  margin-left: 18px;
  background: linear-gradient(94.24deg, #033042 -0.03%, #005c83 99.97%);
  color: #fff;
}

section {
  background: url("images/Vector\ 3.png");
  background-size: cover;
  color: #000;
  display: flex;
  justify-content: space-between;
  gap: 72px;
  padding: 75px 0;
}

.pricing-1,
.pricing-2 {
  width: 50%;
}

.pricing-1 {
  padding-left: 160px;
  position: relative;
}

.pricing-content {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

h2 {
  font-style: normal;
  font-weight: 800;
  font-size: 50px;
  line-height: 64px;
  letter-spacing: -0.03em;
  width: 550px;
}

h2 span {
  color: #005c83;
}

.secondary-heading-text {
  font-size: 18px;
  line-height: 30px;
  margin-top: 30px;
  width: 570px;
}

.pricing-features {
  margin-top: 100px;
  width: 700px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 26px;
}

.pricing-features li {
  width: 300px;
  list-style: none;
  display: inline-block;
  font-size: 18px;
  vertical-align: middle;
}

.pricing-features li p {
  display: inline-block;
  vertical-align: middle;
}

.pricing-features li::before {
  content: "";
  display: inline-block;
  width: 60px;
  height: 60px;
  background-color: #ddd;
  border-radius: 8px;
  margin-right: 18px;
  vertical-align: middle;
}

.pricing-heading {
  display: inline-block;
  font-weight: 600;
  font-size: 22px;
  line-height: 30px;
}

.card {
  width: 500px;
  border-radius: 8px;
  box-shadow: 0px 0px 25px 10px #005c832f;
  
}

.card-header,
.card-footer {
  padding: 35px;
}

.card-header {
  background: url("images/Frame\ 215.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: right;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  border: 1px solid;

  border-image-source: linear-gradient(
    180deg,
    #05445e -19.12%,
    rgba(5, 68, 94, 0) 18.94%
  );
}

.card-footer {
  background-color: #05445e;
  background-image: url("images/1601.m10.i307.n008.F.c05 1.png");
  background-blend-mode: normal;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  color: #fff;
}

.pricing-title {
  display: block;
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 30px;
  margin-bottom: 14px;
}

.pricing-del {
  font-weight: 800;
  font-size: 42px;
  display: block;
  color: #818181;
}

.pricing-main {
  font-weight: 800;
  font-size: 54px;
  display: block;
  background: linear-gradient(90deg, #3f6fc1 0%, #05445e 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.card-footer li {
  list-style: none;
  display: block;
  font-size: 18px;
  margin-bottom: 34px;
}

.card-footer li p {
  width: 320px;
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 200;
  display: inline-block;
  vertical-align: middle;
}

.card-footer li::before {
  content: "";
  display: inline-block;
  width: 60px;
  height: 60px;
  box-shadow: 0px 1px 3px 0.3px rgba(255, 255, 255, 0.652);
  border-radius: 8px;
  margin-right: 18px;
  vertical-align: middle;
}

.pricing-btn {
  padding: 2px;
  display: block;
  width: 100%;
  border-radius: 8px;
  color: #05445e;
  background-color: #fff;
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 800;
  font-size: 17px;
  line-height: 48px;
  text-align: center;
  letter-spacing: 1px;
  box-shadow: 0px 0px 1px 1px rgba(255, 255, 255, 0.3);
}
