.header-img-res {
  display: none;
}

.features-res {
  display: none;
}

.secondary-heading-text-res {
    display: none;
}

@media screen and (max-width: 480px) {
  header {
    padding: 30px;
    padding-top: 50px;
    text-align: center;
  }

  .header-content {
    width: 100%;
    position: relative;
    top: auto;
    left: auto;
    transform: translateY(0);
  }

  .header-img-container {
    width: 100%;
    position: relative;
    top: auto;
    right: auto;
    transform: translateY(0);
  }

  .header-img {
    display: none;
  }

  .header-img-res {
    display: block;
    width: 100%;
    transform: scale(1.5);
  }

  h1 {
    font-style: normal;
    font-weight: 800;
    font-size: 32px;
    line-height: inherit;
    letter-spacing: -0.03em;
  }

  .heading-text {
    width: 100%;
    font-family: "DM Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: initial;
    margin-top: 22px;
  }

  nav {
    margin-top: 32px;
  }

  nav ul li {
    display: block;
  }

  nav a {
    margin-bottom: 18px;
    display: block;
    min-width: 100%;
    border-radius: 3px;
    color: #05445e;
    background-color: #fff;
    font-family: "DM Sans";
    font-style: normal;
    font-weight: 800;
    font-size: 17px;
    line-height: 48px;
    text-align: center;
    letter-spacing: 1px;
    box-shadow: 0px 0px 1px 1px rgba(255, 255, 255, 0.3);
  }

  .features {
    margin-top: 32px;
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 18px;
  }

  .features-pc {
    display: none;
  }

  .features li {
    width: 46%;
    list-style: none;
    display: inline-block;
    font-size: 12px;
    font-family: DM Sans;
    letter-spacing: -0.03em;
    text-align: left;
  }

  .features li p {
    width: 70%;
    display: inline-block;
    vertical-align: middle;
  }

  .features li::before {
    content: "";
    display: inline-block;
    width: 35px;
    height: 35px;
    box-shadow: 0px 0px 2px 0.5px rgba(255, 255, 255, 0.652);
    border-radius: 3px;
    margin-right: 8px;
    vertical-align: middle;
  }

  .button-secondary {
    margin-left: 0;
    background: linear-gradient(94.24deg, #033042 -0.03%, #005c83 99.97%);
    color: #fff;
  }

  section {
    width: 100%;
    background: url("images/Vector\ 3.png");
    background-size: cover;
    color: #000;
    display: block;
    justify-content: auto;
    padding: 30px;
    padding-bottom: 62px;
  }

  .pricing-1,
  .pricing-2 {
    width: 100%;
  }

  .pricing-1 {
    padding-left: 0;
    position: relative;
  }

  .pricing-content {
    position: relative;
    top: auto;
    transform: translateY(0);
  }

  h2 {
    font-style: normal;
    font-weight: 800;
    font-size: 32px;
    line-height: 40px;
    letter-spacing: -0.03em;
    width: 100%;
    text-align: center;
  }

  h2 span {
    color: #005c83;
    display: block;
  }

  .secondary-heading-text {
    font-size: 12px;
    line-height: 18px;
    margin-top: 26px;
    width: 570px;
  }

  .secondary-heading-text-res {
    display: block;
    width: 100%;
    text-align: center;
  }

  .secondary-heading-text-pc {
    display: none;
  }

  .pricing-features {
    margin-top: 30px;
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 10px;
    justify-content: space-between;
  }

  .pricing-features li {
    width: 48%;
    list-style: none;
    display: inline-block;
    font-size: 10px;
    vertical-align: middle;
    letter-spacing: -3%;
    margin-bottom: 8px;
  }

  .pricing-features li p {
    width: 75%;
    display: inline-block;
    vertical-align: middle;
  }

  .pricing-features li::before {
    content: "";
    display: inline-block;
    width: 30px;
    height: 30px;
    background-color: #fff;
    box-shadow: 0px 0px 2px 0.5px #005c832f;
    border-radius: 3px;
    margin-right: 8px;
    vertical-align: middle;
  }

  .pricing-heading {
    display: inline-block;
    font-weight: 600;
    font-size: 12px;
    line-height: 16px;
  }

  .card {
    width: 100%;
    margin-top: 38px;
    border-radius: 3px;
    box-shadow: 0px 0px 25px 10px #005c832f;
  }

  .card-header,
  .card-footer {
    padding: 22px;
  }

  .card-header {
    background: url("images/Frame\ 215.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: right;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    border: 1px solid;

    border-image-source: linear-gradient(
      180deg,
      #05445e -19.12%,
      rgba(5, 68, 94, 0) 18.94%
    );
  }

  .card-footer {
    background-color: #05445e;
    background-image: url("images/1601.m10.i307.n008.F.c05\ 1.png");
    background-blend-mode: normal;
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
    color: #fff;
  }

  .pricing-title {
    display: block;
    font-family: "DM Sans";
    font-style: normal;
    font-weight: 600;
    font-size: 12px;
    line-height: 30px;
    margin-bottom: 0px;
  }

  .pricing-del {
    font-weight: 800;
    font-size: 24px;
    display: block;
    color: #818181;
  }

  .pricing-main {
    font-weight: 800;
    font-size: 30px;
    display: block;
    background: linear-gradient(90deg, #3f6fc1 0%, #05445e 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }

  .card-footer li {
    list-style: none;
    display: block;
    font-size: 12px;
    margin-bottom: 18px;
  }

  .card-footer li p {
    width: 80%;
    font-family: "DM Sans";
    font-style: normal;
    font-weight: 200;
    display: inline-block;
    vertical-align: middle;
  }

  .card-footer li::before {
    content: "";
    display: inline-block;
    width: 35px;
    height: 35px;
    box-shadow: 0px 1px 3px 0.3px rgba(255, 255, 255, 0.652);
    border-radius: 3px;
    margin-right: 18px;
    vertical-align: middle;
  }

  .pricing-btn {
    margin-top: 28px;
    padding: 10px 0;
    display: block;
    width: 100%;
    border-radius: 3px;
    color: #05445e;
    background-color: #fff;
    font-family: "DM Sans";
    font-style: normal;
    font-weight: 800;
    font-size: 16px;
    line-height: 18px;
    text-align: center;
    letter-spacing: 1px;
    box-shadow: 0px 0px 1px 1px rgba(255, 255, 255, 0.3);
  }
}
